import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AnimationService {
num1:number;
num2:number;
  constructor() {
    this.num1 =-45;
    this.num2 =45;
   }
  getnum1(){
    return this.num1;
  }
  getnum2(){
    return this.num2;
  }
  setnum1(n1:number){
    this.num1 = n1;
  }
  setnum2(n2:number){
    this.num2 = n2;
  }
}
